@import '../utils/variables.scss';
@import '../utils/mixins.scss';

button {
    @include column-center;
    text-align: center;
    // min-width: 130px;
    height: 40px;
    color: #fff;
    // padding: 10px 10px;
    margin: 5px 5px;
    font-size: medium;
    font-weight: bold;
    cursor: pointer;
    transition: all 0.3s ease;
    position: relative;
    display: inline-block;
    outline: none;
    overflow: hidden;
    box-sizing: border-box;
    border-radius: 5px;
    border: none;
    background-color: $pastel-yellow;
    text-decoration: none;
    text-align: center;
    line-height: 40px;
};
.button-in-main {
    background-color: $light-green;
    padding: 5px;
}